的同學的需求，要你只幫助他。這些情況超過了一個界限後，就必須明確拒絕。同時可以透過角色扮演、設身處地的引導，培養孩子的同理心。")]),_c('p',[_vm._v("應確保：秩序的明確底限。成人自己感受的明確表達。堅定感。")]),_c('p',[_vm._v("應避免：坐視孩子霸凌別人、搖擺不定。")]),_c('h2',[_vm._v("眼高手低期(約1~2年)")]),_c('p',[_vm._v("──第三階段是眼高手低期。開始知道自己的方向和目標，但是發現自己的能力和目標非常遠。想得到卻做不到，也對於如何建立自己的學習習慣、克服慣性容易感到「我知道我應該，可是我很難去做」的挫折。")]),_c('img',{attrs:{"src":require("../assets/depressed.png")}}),_c('h3',[_vm._v("原因探討：")]),_c('p',[_vm._v("當孩子確立了邊界，知道自己不是無所不能，需要學習和成長才能達成目標，會一下子感受到自己的能力其實很小。")]),_c('h3',[_vm._v("親子對應要訣：")]),_c('p',[_vm._v("很多孩子在這時候會出現明顯低落的自我認知，和停滯的學習進程。有些父母因此著急並放棄陪孩子學習自律，送回體制學校或機構。")]),_c('p',[_vm._v("其實，當孩子經歷到第三階段，就會羽化之前的蛹期，只要成人清楚、溫暖的接納，陪孩子一起找尋適切方法來支持，要啟動正向轉習循轉就不遠了。")]),_c('p',[_vm._v("有些父母會熬不過這些階段，應付不了自己心理的矛盾或是親友的壓力，中途撤退，也有些父母一開始抱著趕流行的心態，等到孩子的轉型開始後，又覺得後悔，這樣的搖擺，對孩子其實是有傷害的。")]),_c('p',[_vm._v("在蛹期，要看到孩子能夠瞭解自己的「眼高手低」，就是代表他的自覺出來了，是好事，可以陪他從基礎開始，一步一步把手練高。")]),_c('p',[_vm._v("應確保：陪孩子把他們目標切細，用小階梯的方式，一步一腳印，逐步實現目標，累積踏實的成就感。")]),_c('p',[_vm._v("應避免：過度否定孩子、用更大的目標來指責孩子為什麼達不到。")]),_c('h3',[_vm._v("師生對應要訣：")]),_c('p',[_vm._v("應確保：小階梯教學、明確診斷出學習的問題點加以支援、分享自己的習慣建立經驗。")]),_c('p',[_vm._v("應避免：放低目標、不具體的安慰、唬弄學生。")]),_c('h2',[_vm._v("自主成熟期(一輩子)")]),_c('img',{attrs:{"src":require("../assets/blind.png")}}),_c('p',[_vm._v("──第四階段之後，孩子對人對事的包容力都會增加，有自信、有光彩、講道理、守規距也知變通。")]),_c('p',[_vm._v("因為第四階段的美好，常讓人嚮往自己的孩子如果也能如此多好。")]),_c('p',[_vm._v("但是自主學習，是從學習自主開始的。")]),_c('p',[_vm._v("學習自主，不只是孩子的事，也是家長和所有協力的教師都要經歷的過程。")]),_c('p',[_vm._v("兩到四年的過程，造就一輩子的獨立自主，這樣想想，認真陪伴自己、陪伴孩子渡過散漫的解放期、討厭的自我中心期、挫折的眼高手低期，也就值得了吧！")])])])}]
